import Vue from 'vue'
import Vuex from 'vuex'
import Search from "../services/YoutubeSearch";

Vue.use(Vuex);

const store = new Vuex.Store({

  state: {
    video: "",
    videoId: "",
    url: "",
    videos: "",
    searchTerm: "",
    nextPageToken: "",
    endOfPage: true,
    searchDone: false
  },
  mutations: {
    setVideo(state, video) {
      state.video = video;
      state.videoId = video.id.videoId;
      state.url = `https://www.youtube.com/embed/${video.id.videoId}`;
    },

    setVideos(state, videos) {
      state.videos = videos;
    },

    setSearchTerm(state, searchTerm) {
      state.searchTerm = searchTerm;
    },

    setSearchDone(state, searchDone) {
      state.searchDone = searchDone;
    },

    setNextPageToken(state, nextPageToken) {
      state.nextPageToken = nextPageToken;
    },

    concatVideos(state, videos){
      state.videos = this.state.videos.concat(videos);
    },

    setEndOfPage(state, endOfPage) {
      state.endOfPage = endOfPage;
    },
  },

  getters: {
    getSearchTerm: state => {
      return state.searchTerm
    },
    getNextPageToken: state => {
      return state.nextPageToken
    }
  },

  actions: {
    selectedVideo(context, video) {
      context.commit('setVideo', video);
    },

    searchVideos(context, searchTerm) {

      context.commit('setSearchDone', false);
      
      Search(
        {
          apiKey: process.env.VUE_APP_YOUTUBE_KEY,
          term: searchTerm,
          nextPageToken: this.state.nextPageToken,
        },
        (response) => {
          context.commit('setVideos', response.items);
          context.commit('setSearchDone', true);
          context.commit('setNextPageToken', response.nextPageToken);
          context.commit('setSearchTerm', searchTerm);
        }
      );


    },

    searchNextPage({state, commit}){
      
      commit('setEndOfPage', false);

      Search(
        {
          apiKey: process.env.VUE_APP_YOUTUBE_KEY,
          term: state.searchTerm,
          nextPageToken: state.nextPageToken,
        },
        (response) => {
          commit('concatVideos', response.items);
          commit('setEndOfPage', true);
          commit('setNextPageToken', response.nextPageToken);
        }
      ); 
    }
  }
});

export { store }